.post-it-paper {
  font-family: 'Reenie Beanie', 'Waiting for the Sunrise', 'Marker Felt', 'Comic Sans', serif;
  font-size: 28px;
  text-align: center;
  line-height: 1;
  position: relative;
  display: inline-block;
  /* Sizing */
  margin: 8px;
  width: 275px;
  /* set width to 275px or 250px */
  min-height: 250px;
  /* set height to 245px or 250px */
  max-height: 250px;
  padding-top: 0px;
  /* example: set padding-top to 35px to center two lines */
  /* Yellow Hex Color */
  background: #ffff88;
  /* FF3.6+, (Chrome10+, Safari5.1+), Opera 11.10+, IE10+ */
  /* Chrome, Safari4+ */
  background: linear-gradient(135deg, #ffff88 81%, #ffff88 82%, #ffff88 82%, #ffffc6 100%);
  /* W3C */
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ffff88', endColorstr='#ffffc6', GradientType=1);
  /* IE6-9 fallback on horizontal gradient */
  /* Post-its have that sticky glue-like substance on top that, when in use, has a slightly different look than the rest of the Post-it */
  border: 1px solid #E8E8E8;
  border-top: 60px solid #fdfd86;
  /* Peeled Corner: the bottom corners tend to peel off the surface a bit when stuck to something.
  This 3D effect is subtle, but adds a ton to the feel of the note. Not IE compatible */
  border-bottom-right-radius: 60px 5px;
}

.post-it-paper:after {
  content: "";
  position: absolute;
  z-index: -1;
  right: -0px;
  bottom: 20px;
  width: 200px;
  height: 25px;
  background: rgba(0, 0, 0, 0.2);
  box-shadow: 2px 15px 5px rgba(0, 0, 0, 0.4);
  transform: matrix(-1, -0.1, 0, 1, 0, 0);
  -webkit-transform: matrix(-1, -0.1, 0, 1, 0, 0);
  -moz-transform: matrix(-1, -0.1, 0, 1, 0, 0);
  -ms-transform: matrix(-1, -0.1, 0, 1, 0, 0);
  -o-transform: matrix(-1, -0.1, 0, 1, 0, 0);
}

.post-it-paper p {
  position: absolute;
}

.post-it-paper p:nth-of-type(2) {
  color: red;
  bottom: 0;
  right: 30px;
}

nav.main-nav {
  position: -webkit-sticky !important;
  position: sticky !important;
  top: 0;
  z-index: 1000 !important;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #262929;
}

nav.main-nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

nav.main-nav ul li {
  float: left;
}

nav.main-nav ul li.right {
  float: right;
}

nav.main-nav ul li a {
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

nav.main-nav ul li a:hover:not(.active) {
  background-color: #565f5f;
}

nav.main-nav ul li a.active {
  background-color: #c1946a;
}

@media screen and (max-width: 600px) {
  nav.main-nav {
    position: relative !important;
  }
  nav.main-nav ul li.right, nav.main-nav ul li {
    float: none;
  }
}

