/* Experience Page */
.lined {
  height: 100%;
  width: 100%;
  padding: 0;
  position: relative;
}

.lined li {
  /* each experience item */
  list-style-type: none;
  width: calc(90rem + (100% - 90rem));
  overflow-y: hidden;
  min-height: 11vw;
}

.lined li .centered {
  width: calc(15rem + ((100% - 90rem) / 1.1 * 0.1));
  display: inline-block;
}

.lined li .centered:hover div::before {
  transform: scale(1.1);
  opacity: 1;
  transition: .3s opacity ease-in-out, .3s transform ease-in-out;
  position: absolute;
  width: 10vw;
  height: 10vw;
  z-index: -1;
  content: ' ';
  box-sizing: border-box;
  display: block;
  background: #FFF;
  border: 0.2vw solid #405d27;
  border-radius: 100%;
}

.lined li article {
  width: calc(37.5rem + ((100% - 90rem) / 1.1 * 0.5));
  min-width: calc(37.5rem + ((100% - 90rem) / 1.1 * 0.5));
  max-width: calc(37.5rem + ((100% - 90rem) / 1.1 * 0.5));
  background-color: #262929;
  border-radius: 20px;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -ms-border-radius: 20px;
  -o-border-radius: 20px;
  padding-left: 5px;
  color: #82b74b;
  font-size: 2rem;
}

.lined li article h1 {
  color: #82b74b;
  font-size: 2.5rem;
}

.lined li:nth-of-type(odd) .centered {
  margin-left: calc(37.5rem + ((100% - 90rem) / 1.1 * 0.5));
}

.lined li:nth-of-type(odd) article {
  float: right;
}

.lined li:nth-of-type(even) article {
  float: left;
}

.lined::before {
  /* horizontally centered vertical line */
  position: absolute;
  top: 0;
  left: 50%;
  height: 100%;
  width: 1px;
  background: #AAA;
  content: ' ';
  z-index: -3;
}

.lined #work-petitcode div div:hover {
  background-image: url("../assets/images/large/petitcode.png");
}

.lined #work-virtus div div:hover {
  background-image: url("../assets/images/large/virtus.jpg");
}

.lined #bachelor-ufcg div div:hover {
  background-image: url("../assets/images/vectors/ufcg.svg");
  background-size: contain;
}

.lined #research-tud div div:hover {
  background-image: url("../assets/images/vectors/tu_dresden.svg");
}

.lined #research-lsd div div:hover {
  background-image: url("../assets/images/large/lsd.png");
}

.lined #research-splab div div:hover {
  background-image: url("../assets/images/large/splab.png");
}

@media only screen and (max-width: 425px) {
  .lined {
    height: 700px;
  }
  .lined li .centered div {
    position: absolute;
    left: 25%;
    margin: 0 0 20px -21%;
    padding: 0;
  }
  .lined li article {
    clear: both;
    min-width: 66.6vw !important;
    display: inline-block;
    margin-top: -10%;
    padding-left: 5px;
    margin-bottom: 10%;
    float: right !important;
    background-color: unset;
    color: unset;
  }
  .lined li article h1, .lined li article p {
    font-size: 1rem;
    color: unset;
  }
  .lined::before {
    /* left vertical line */
    left: 10%;
  }
}

@media only screen and (min-width: 426px) and (max-width: 768px) {
  .lined h1, .lined p {
    font-size: 1rem;
  }
}

